<template>
	<view class="box">
		<view class="list"  @click="enterDetail(id)">
			<view class="surgical_image">
				<image :src="imgUrl" mode="aspectFit"></image>
			</view>
			<view class="content">
				<view>
					<text class="content_titlename">{{content_title}}</text>
					<view>
						<text class="content_item">{{content}}</text>
					</view>
				</view>
				<view class="title">
					<view class="title_top">
						<image :src="iconUrl" mode=""></image>
						<text>{{docName}}</text>
					</view>
					<view class="time">
						<text>{{time|timeStamp}}</text>
					</view>	
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
			};
		},
		props:{
			id:{
				type:Number,
				default:'',
			},
			iconUrl:{
				type:String,
				default:'',
			},
			docName:{
				type:String,
				default:'',
			},
			time:{
				type:Number,
				default:'',
			},
			imgUrl:{
				type:String,
				default:'',
			},
			content_title:{
				type:String,
				default:'',
			},
			content:{
				type:String,
				default:'',
			}
		},
		filters: {
		  timeStamp: function (timestamp) {
			var date = new Date(timestamp);
			var y = date.getFullYear();
			var m = date.getMonth() + 1;
			m = m < 10 ? ('0' + m) : m;
			var d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			var h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			var minute = date.getMinutes();
			var second = date.getSeconds();
			minute = minute < 10 ? ('0' + minute) : minute;
			second = second < 10 ? ('0' + second) : second;
			return y + '-' + m + '-' + d;
		  }
		},
		methods:{
			enterDetail(id){
				console.log(id)
				uni.navigateTo({
					url:'/pages/surgicalData/surgicalDetail?id='+id,
					animationType:'pop-in'
				})
			}
		}
	}
</script>

<style>
	.box{
		padding: 0 30rpx;
		margin-bottom: 30rpx;
	}
	.list{
		display: flex;
		align-items: flex-start;
		padding: 18rpx 12rpx;
		background-color: #FFFFFF;
		border: 1px solid #EEEEEE;
		border-radius: 10rpx;
	}
	.surgical_image{
		height: 214rpx;
		width: 226rpx;
		margin-right: 21rpx;
	}
	.surgical_image image{
		width: 100%;
		height: 214rpx;
		background: #adabff;	
		/* border: 1px solid #EEEEEE; */
	}
	.content{
		flex:1;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 214rpx;
		/* height: 100%; */
		/* overflow: hidden; */
		margin-top: 0;
	}
	.content .content_titlename{
		color: #000000;
		font-size: 30rpx;
		font-weight: bold;
		font-family: PingFang-SC-Bold;
		/* margin-bottom: 22rpx; */
		/* line-height: 24rpx; */
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		overflow: hidden;
	}
	.content .content_item{
		color: #000000;
		font-family: PingFang-SC-Regular;
		font-size: 24rpx;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
		overflow: hidden;
		line-height: 30rpx;
		margin: 22rpx 0 30rpx;

	}
	.title{
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: row;
		color: #000000;
		font-size: 24rpx;
		font-family: PingFang-SC-Regular;
	}
	.title .time{
		color: #909090;
	}
	.title_top{
		display: flex;
		align-items: center;
	}
	.title_top image{
		width: 36rpx;
		height: 36rpx;
		border-radius: 50%;
		margin-right: 7px;
		display: block;
	}
	
</style>
